/* Fonts - Roboto and Open Sans*/
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

/* Media Breakpoints */
@import "./media-variables.scss";

// Convert CSS variables to SASS later 
:root {
  --background-color: #E5E5E5;

  --navbar-background: #313A46;
  --navbar-text: #FFFFFF;
  --navbar-text-alt: #FFFFFF;

  --line-accent: #3974BB;
  --btn-primary: #59AFD4;
  --btn-rollover: #82C9EB;

  --breakout-background: #313A46;
  --feature-underline: #82C9EB;
  --breakout-text: #E5E5E5;

  --footer-background: #313A46;
  --footer-accent: #82C9EB;
  --footer-links: #e4e4e4;
  --footer-links-hover: #82C9EB;
  --footer-headers: #6CAED0;
}

body {
  font-family: 'Roboto', sans-serif;
  /* font-family: 'Open Sans', sans-serif; */

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: var(--background-color);
}

.container-fluid {
  padding: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.btn {
  border-radius: 20px;
}

.btn-primary {
  background-color: var(--btn-primary);
  border-color: var(--btn-primary);
  transition: background-color 1s, border-color 1s;
}

.btn-primary:hover {
  border-color: var(--btn-rollover);
  background-color: var(--btn-rollover);
}

hr {
  opacity: 1.0;
}

/* ----- NAVBAR STYLING ----- */
.navbar {
  background-color: none;
  transition: background-color 1s ease, filter 1s ease;

  &.scrolled {
    background-color: var(--navbar-background);
    filter: drop-shadow(0px 1px 8px #000000);

    .navbar-brand {
      color: var(--navbar-text);
    }
  }

  .navbar-brand {
    padding-left: 60px;
    color: var(--navbar-text-alt);
    transition: color 1s ease;

    .navbar-logo {
      max-height: 50px;
    }
  }

  .navbar-collapse {
    justify-content: right;

    .nav-item {
      padding: 5px 20px 5px 20px;

      .nav-link {
        font-weight: 400;
        font-size: 1.25em;
        color: var(--navbar-text-alt);
        opacity: 0.7;
        transition: color 1s ease, opacity 1s ease;
      
        &.active {
          opacity: 1.0;
        }

        &.scrolled {
          color: var(--navbar-text);

          &:hover {
            color: var(--navbar-text-alt)
          }
        }
      
        &:hover {
          opacity: 1.0;
        }
      }

      button {
        border: 1px solid var(--navbar-text-alt);
        background-color: none;
        transition: background-color 1s ease, color 1s ease, border 1s ease;
        @extend .nav-link;

        &:hover {
          background-color: var(--navbar-text-alt);
          color: var(--navbar-background);
          border: 1px solid var(--navbar-text);
        }
      }
    }
  }
}

// MOBILE BREAKPOINT
@media (max-width: $screen-sm-max) {
  .navbar {
    background-color: var(--navbar-background);
    filter: drop-shadow(0px 1px 8px #000000);

    .navbar-brand {
      color: var(--navbar-text-alt);
      padding-left: 20px;
    }

    .navbar-toggler {
      margin: 5px 20px 5px 0px;
      border: 2px solid var(--navbar-text-alt);
      svg {
        fill: var(--navbar-text-alt);
      }
    }

    .navbar-collapse {
      border-top: 1px solid white;
      margin-top: 10px;
    }
  }
}

// TABLET BREAKPOINT
@media (max-width: $screen-md-max) {

}

/* ----- SUBHEADER COMPONENT STYLING ----- */

.subheader-container {
  margin: 0px;
  text-align: center;
  justify-content: center;
  
  .subheader {
    padding: 20px 0px 20px 0px;
    font-size: 1.5em;
    max-width: 80%;
  }

  .subtext {
    padding: 0px 0px 10px 0px;
    font-size: 1em;
    max-width: 80%;
  }

  hr {
    height: 2px;
    background-color: var(--line-accent);
    width: 60vw;
    margin: auto;
  }
}

/* ----- TRI-COLUMN COMPONENT STYLING ----- */

.tricolumn-container {
  margin: 0px;
  padding: 40px 20px 40px 20px;

  &.dark {
    background-color: var(--breakout-background);
    color: #FFFFFF;
  }

  .info-column {
    padding: 0px 40px 0px 40px;

    h3 {
      font-size: 1.5em;
      padding: 0px 0px 5px 0px;
    }

    p {
      font-size: 1em;
    }
  }
}

// MOBILE BREAKPOINT
@media (max-width: $screen-sm-max) {
  .tricolumn-container {
    padding: 10px 10px 10px 10px;
  }
}