#contact-modal {
    .modal-content {
        .contact-welcome {
            text-align: center;
        }

        form {
            display: flex;
            flex-direction: column;

            .form-group {
                padding: 10px 20px 10px 20px;
            }

            button {
                margin: auto;
            }
        }

        .modal-footer {
            justify-content: center;
        }
    }
}