:root{--background-color: #E5E5E5;--navbar-background: #313A46;--navbar-text: #FFFFFF;--navbar-text-alt: #FFFFFF;--line-accent: #3974BB;--btn-primary: #59AFD4;--btn-rollover: #82C9EB;--breakout-background: #313A46;--feature-underline: #82C9EB;--breakout-text: #E5E5E5;--footer-background: #313A46;--footer-accent: #82C9EB;--footer-links: #e4e4e4;--footer-links-hover: #82C9EB;--footer-headers: #6CAED0}body{font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#E5E5E5;background-color:var(--background-color)}.container-fluid{padding:0px}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.btn{border-radius:20px}.btn-primary{background-color:#59AFD4;background-color:var(--btn-primary);border-color:#59AFD4;border-color:var(--btn-primary);transition:background-color 1s, border-color 1s}.btn-primary:hover{border-color:#82C9EB;border-color:var(--btn-rollover);background-color:#82C9EB;background-color:var(--btn-rollover)}hr{opacity:1.0}.navbar{background-color:none;transition:background-color 1s ease, -webkit-filter 1s ease;transition:background-color 1s ease, filter 1s ease;transition:background-color 1s ease, filter 1s ease, -webkit-filter 1s ease}.navbar.scrolled{background-color:#313A46;background-color:var(--navbar-background);-webkit-filter:drop-shadow(0px 1px 8px #000);filter:drop-shadow(0px 1px 8px #000)}.navbar.scrolled .navbar-brand{color:#FFFFFF;color:var(--navbar-text)}.navbar .navbar-brand{padding-left:60px;color:#FFFFFF;color:var(--navbar-text-alt);transition:color 1s ease}.navbar .navbar-brand .navbar-logo{max-height:50px}.navbar .navbar-collapse{justify-content:right}.navbar .navbar-collapse .nav-item{padding:5px 20px 5px 20px}.navbar .navbar-collapse .nav-item .nav-link,.navbar .navbar-collapse .nav-item button{font-weight:400;font-size:1.25em;color:#FFFFFF;color:var(--navbar-text-alt);opacity:0.7;transition:color 1s ease, opacity 1s ease}.navbar .navbar-collapse .nav-item .nav-link.active,.navbar .navbar-collapse .nav-item button.active{opacity:1.0}.navbar .navbar-collapse .nav-item .nav-link.scrolled,.navbar .navbar-collapse .nav-item button.scrolled{color:#FFFFFF;color:var(--navbar-text)}.navbar .navbar-collapse .nav-item .nav-link.scrolled:hover,.navbar .navbar-collapse .nav-item button.scrolled:hover{color:#FFFFFF;color:var(--navbar-text-alt)}.navbar .navbar-collapse .nav-item .nav-link:hover,.navbar .navbar-collapse .nav-item button:hover{opacity:1.0}.navbar .navbar-collapse .nav-item button{border:1px solid #FFFFFF;border:1px solid var(--navbar-text-alt);background-color:none;transition:background-color 1s ease, color 1s ease, border 1s ease}.navbar .navbar-collapse .nav-item button:hover{background-color:#FFFFFF;background-color:var(--navbar-text-alt);color:#313A46;color:var(--navbar-background);border:1px solid #FFFFFF;border:1px solid var(--navbar-text)}@media (max-width: 991px){.navbar{background-color:#313A46;background-color:var(--navbar-background);-webkit-filter:drop-shadow(0px 1px 8px #000);filter:drop-shadow(0px 1px 8px #000)}.navbar .navbar-brand{color:#FFFFFF;color:var(--navbar-text-alt);padding-left:20px}.navbar .navbar-toggler{margin:5px 20px 5px 0px;border:2px solid #FFFFFF;border:2px solid var(--navbar-text-alt)}.navbar .navbar-toggler svg{fill:#FFFFFF;fill:var(--navbar-text-alt)}.navbar .navbar-collapse{border-top:1px solid white;margin-top:10px}}.subheader-container{margin:0px;text-align:center;justify-content:center}.subheader-container .subheader{padding:20px 0px 20px 0px;font-size:1.5em;max-width:80%}.subheader-container .subtext{padding:0px 0px 10px 0px;font-size:1em;max-width:80%}.subheader-container hr{height:2px;background-color:#3974BB;background-color:var(--line-accent);width:60vw;margin:auto}.tricolumn-container{margin:0px;padding:40px 20px 40px 20px}.tricolumn-container.dark{background-color:#313A46;background-color:var(--breakout-background);color:#FFFFFF}.tricolumn-container .info-column{padding:0px 40px 0px 40px}.tricolumn-container .info-column h3{font-size:1.5em;padding:0px 0px 5px 0px}.tricolumn-container .info-column p{font-size:1em}@media (max-width: 991px){.tricolumn-container{padding:10px 10px 10px 10px}}

.hero-container{padding:50px 50px 20px 50px;position:relative;overflow-x:clip}.hero-description-container{max-width:50vw}.hero-header{font-weight:800}.hero-navigation .navbar .navbar-nav{display:flex;flex-direction:row}.hero-navigation .nav-item{padding:10px}.hero-navigation hr{border:2px solid var(--line-accent);margin:20px 0px 0px 0px;max-width:60vw}.hero-subheading-container{padding:10px 10px 10px 10px}.hero-subheading-body{font-size:0.75em}.hero-subtext{font-size:1.5em}@media all and (max-width: 30em){.hero-description-container{max-width:100vw}.hero-navigation hr{max-width:80vw}.hero-container{padding:40px 20px 10px 20px}.hero-subtext{font-size:1em}.hero-subheaders{display:none}}

#waves{-webkit-transform:rotate(-160deg);transform:rotate(-160deg);transition:0.3s;position:absolute;top:-30vw;left:-20vw;width:150vw;z-index:-1}@media (min-width: 1599px){#waves{-webkit-transform:rotate(-175deg);transform:rotate(-175deg);width:120vw;top:-600px;left:-200px}}@media (max-width: 1600px){#waves{-webkit-transform:rotate(-170deg);transform:rotate(-170deg);width:120vw;top:-250px;left:-50px}}@media (max-width: 1199px){#waves{-webkit-transform:rotate(-160deg);transform:rotate(-160deg);width:150vw;top:-225px;left:-150px}}@media (max-width: 991px){#waves{-webkit-transform:rotate(-140deg);transform:rotate(-140deg);width:250vw;top:-50px;left:-200px}}.wave{-webkit-animation:move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;animation:move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite}#wave-1{-webkit-transform:translate(0, 0px);transform:translate(0, 0px);opacity:1;-webkit-animation-delay:-2s;animation-delay:-2s;-webkit-animation-duration:7s;animation-duration:7s}#wave-2{-webkit-transform:translate(0, 50px);transform:translate(0, 50px);opacity:0.9;-webkit-animation-delay:-3s;animation-delay:-3s;-webkit-animation-duration:10s;animation-duration:10s}#wave-3{-webkit-transform:translate(0, 100px);transform:translate(0, 100px);opacity:0.8;-webkit-animation-delay:-4s;animation-delay:-4s;-webkit-animation-duration:13s;animation-duration:13s}#wave-4{-webkit-transform:translate(0, 150px);transform:translate(0, 150px);opacity:0.7;-webkit-animation-delay:-5s;animation-delay:-5s;-webkit-animation-duration:20s;animation-duration:20s}@-webkit-keyframes move-forever{0%{-webkit-transform:translate3d(-90px, 0, 0);transform:translate3d(-90px, 0, 0)}50%{-webkit-transform:translate3d(85px, 0, 0);transform:translate3d(85px, 0, 0)}100%{-webkit-transform:translate3d(-90px, 0, 0);transform:translate3d(-90px, 0, 0)}}@keyframes move-forever{0%{-webkit-transform:translate3d(-90px, 0, 0);transform:translate3d(-90px, 0, 0)}50%{-webkit-transform:translate3d(85px, 0, 0);transform:translate3d(85px, 0, 0)}100%{-webkit-transform:translate3d(-90px, 0, 0);transform:translate3d(-90px, 0, 0)}}

.breakout-section {
    display: flex;
    flex-direction: column;
    justify-content: center;

    background-color: var(--breakout-background);

    color: var(--breakout-text);

    margin: 20px 0px 20px 0px;
}

.breakout-section .section-header {
    padding: 30px 10px 10px 10px;
    text-align: center;
}

.breakout-section .feature-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.breakout-section .feature {
    padding: 20px;
    /* max-width: 30vw; */
}

.breakout-section .feature .feature-text {
    font-size: 1em;
}

.breakout-section .section-subtext {
    padding: 10px 20px 30px 20px;
    text-align: center;
    font-size: 1.25em;
}

.breakout-section .feature hr {
    border: 1px solid var(--feature-underline)
}
.content-container{margin:auto;padding:30px 30px 30px 30px}.content-container .section-header{font-weight:600;padding-bottom:10px}.content-container .image-container{display:flex;flex-direction:row;justify-content:center}.content-container .image-container img{max-width:100%;max-height:450px;margin:auto}@media (max-width: 991px){.content-container .text-column .section-header{padding-top:20px;font-size:1.25em}.content-container .text-column .section-subheader{font-size:1.1em}.content-container .text-column .section-text{font-size:0.9em}}@media (max-width: 1199px){.content-container .text-column .section-header{font-size:1.35em}.content-container .text-column .section-text{font-size:1em}}

.footer-container{background-color:var(--footer-background);padding:80px 80px 40px 80px;justify-content:center}.footer-container hr{height:1px;background-color:var(--footer-accent)}.footer-container .footer-menus{padding:0px 0px 20px 0px;justify-content:center}.footer-container .footer-menus .footer-menu{display:flex;flex-direction:column}.footer-container .footer-menus .footer-menu hr{width:60%;margin:5px 0px 10px 0px}.footer-container .footer-menus .footer-menu .menu-header{font-size:1.25em;color:var(--footer-headers)}.footer-container .footer-menus .footer-menu .footer-menu-items{font-size:1em;padding:10px 0px 0px 0px;color:var(--footer-links);text-decoration:none;transition:color 1s ease}.footer-container .footer-menus .footer-menu .footer-menu-items:hover{color:var(--footer-links-hover)}.footer-container .social-links{display:flex;flex-direction:column;text-align:center;padding:20px 0px 10px 0px}.footer-container .social-links .social-links-header{font-size:1.5em;color:var(--footer-headers)}.footer-container .social-links .social-icons-container{display:flex;flex-direction:row;justify-content:center}.footer-container .social-links .social-icons-container .social-icon{font-size:2em;padding:0px 20px 0px 20px;color:var(--footer-links);transition:color 1s ease}.footer-container .social-links .social-icons-container .social-icon:hover{color:var(--footer-links-hover)}.footer-container .legal-information{display:flex;flex-direction:row}.footer-container .legal-information .legal-item{max-height:40px;padding:0px 15px 0px 15px;color:var(--footer-links);text-decoration:none;transition:color 1s ease}.footer-container .legal-information .legal-item:hover{color:var(--footer-links-hover)}@media (max-width: 991px){.footer-container{padding:50px 30px 30px 30px}.footer-container .footer-menus{text-align:center}.footer-container .footer-menus .footer-menu{padding:10px 0px 10px 0px}.footer-container .footer-menus .footer-menu hr{margin:auto}.footer-container .legal-information{font-size:0.75em}}

#contact-modal .modal-content .contact-welcome{text-align:center}#contact-modal .modal-content form{display:flex;flex-direction:column}#contact-modal .modal-content form .form-group{padding:10px 20px 10px 20px}#contact-modal .modal-content form button{margin:auto}#contact-modal .modal-content .modal-footer{justify-content:center}

.pdf-container .pdf-toolbar{max-width:90vw;margin:auto;margin-top:50px;text-align:center}.pdf-container .pdf-toolbar .toolbar-row{display:flex;flex-direction:row;justify-content:space-evenly;align-items:center;margin-bottom:20px;border:2px solid #313A46;border-radius:20px}.pdf-container .pdf-toolbar .toolbar-row p{margin:0px;font-weight:500}.pdf-container .pdf-toolbar .toolbar-row .btn{width:100px;margin:10px}.pdf-container .pdf-document{max-width:90vw;margin:auto;padding-bottom:50px}

