.hero-container {    
    padding: 50px 50px 20px 50px;
    
    // prevent waves from overflowing
    position: relative;
    overflow-x: clip;
}

.hero-description-container {
    max-width: 50vw;
}

.hero-header {
    font-weight: 800;
}

.hero-navigation .navbar .navbar-nav {
    display: flex;
    flex-direction: row;
}

.hero-navigation .nav-item {
    padding: 10px;
}

.hero-navigation hr {
    border: 2px solid var(--line-accent);
    margin: 20px 0px 0px 0px;
    max-width: 60vw;
}

.hero-subheading-container {
    padding: 10px 10px 10px 10px;
}

.hero-subheading-body {
    font-size: 0.75em;
}

.hero-subtext {
    font-size: 1.5em;
}

/* Mobile Breakpoint */
@media all and (max-width: 30em) {
    .hero-description-container {
        max-width: 100vw;
    }

    .hero-navigation hr {
        max-width: 80vw;
    }

    .hero-container {
        padding: 40px 20px 10px 20px;
    }

    .hero-subtext {
        font-size: 1em;
    }

    /* IMPROVE ME */
    .hero-subheaders {
        display: none;
    }
}