@import "../../media-variables.scss";

.content-container {
    margin: auto;
    padding: 30px 30px 30px 30px;

    .section-header {
        font-weight: 600;
        padding-bottom: 10px;
    }

    .image-container {
        display: flex;
        flex-direction: row;
        justify-content: center;

        img {
            max-width: 100%;
            max-height: 450px;
            margin: auto;
        }
    }
}

// MOBILE BREAKPOINT
@media (max-width: $screen-sm-max) {
    .content-container {
        .text-column {
            .section-header {
                padding-top: 20px;
                font-size: 1.25em;
            }

            .section-subheader {
                font-size: 1.1em;
            }

            .section-text {
                font-size: 0.9em;
            }
        }
    }
}

// TABLET BREAKPOINT
@media (max-width: $screen-md-max) {
    .content-container {
        .text-column {
            .section-header {
                font-size: 1.35em;
            }

            .section-text {
                font-size: 1em;
            }
        }
    }
}