@import "../../media-variables.scss";

.footer-container {
    background-color: var(--footer-background);
    padding: 80px 80px 40px 80px;
    justify-content: center;

    hr {
        height: 1px;
        background-color: var(--footer-accent);
    }

    .footer-menus {
        padding: 0px 0px 20px 0px;
        justify-content: center;

        .footer-menu {
            display: flex;
            flex-direction: column;

            hr {
                width: 60%;
                margin: 5px 0px 10px 0px;
            }

            .menu-header {
                font-size: 1.25em;
                color: var(--footer-headers);
            }

            .footer-menu-items {
                font-size: 1em;
                padding: 10px 0px 0px 0px;
                color: var(--footer-links);
                text-decoration: none;
                transition: color 1s ease;

                &:hover {
                    color: var(--footer-links-hover);
                }
            }
        }
    }

    .social-links {
        display: flex;
        flex-direction: column;
        text-align: center;
        padding: 20px 0px 10px 0px;

        .social-links-header {
            font-size: 1.5em;
            color: var(--footer-headers);
        }

        .social-icons-container {
            display: flex;
            flex-direction: row;
            justify-content: center;

            .social-icon {
                font-size: 2em;
                padding: 0px 20px 0px 20px;
                color: var(--footer-links);
                transition: color 1s ease;

                &:hover {
                    color: var(--footer-links-hover);
                }
            }
        }
    }

    .legal-information {
        display: flex;
        flex-direction: row;

        .legal-item {
            max-height: 40px;
            padding: 0px 15px 0px 15px;
            color: var(--footer-links);
            text-decoration: none;
            transition: color 1s ease;

            &:hover {
                color: var(--footer-links-hover);
            }
        }
    }
}

// MOBILE BREAKPOINT
@media (max-width: $screen-sm-max) {
    .footer-container {
        padding: 50px 30px 30px 30px;

        .footer-menus {
            text-align: center;

            .footer-menu {
                padding: 10px 0px 10px 0px;

                hr {
                    margin: auto;
                }
            }
        }

        .legal-information {
            font-size: 0.75em;
        }
    }
}