.pdf-container {
    .pdf-toolbar {
        max-width: 90vw;
        margin: auto;
        margin-top: 50px;
        text-align: center;

        .toolbar-row {
            display: flex;
            flex-direction: row;
            justify-content: space-evenly;
            align-items: center;
            margin-bottom: 20px;

            // background-color: #313A46;
            border: 2px solid #313A46;
            border-radius: 20px;

            p {
                margin: 0px;
                font-weight: 500;
            }

            .btn {
                width: 100px;
                margin: 10px;
            }
        }
    }

    .pdf-document {
        max-width: 90vw;
        margin: auto;
        padding-bottom: 50px;
    }
}